.container span.radio:hover {
  cursor: pointer;
}
.container span.radio::before {
  left: -52px;
  width: 45px;
  height: 25px;
  background-color: #A8AAC1;
  border-radius: 50px;
}
.container span.radio::after {
  left: -49px;
  width: 17px;
  height: 17px;
  border-radius: 10px;
  background-color: #6C788A;
  -webkit-transition: left .25s, background-color .25s;
  transition: left .25s, background-color .25s;
}
input[type="checkbox"]:checked + label span.checkbox::after {
  background:#f2dede;
  cursor:not-allowed;
  animation:flashBooked 0.5s ease;
  -moz-animation:flashBooked 0.5s ease;
  -webkit-animation:flashBooked 0.5s ease;
}
input[type="checkbox"]:not(:checked) + label span.checkbox::after {
  background:#6AB074;
  animation:flashAvailable 0.5s ease;
  -moz-animation:flashAvailable 0.5s ease;
  -webkit-animation:flashAvailable 0.5s ease;
}
